import { AfterViewInit, ChangeDetectorRef, Component, EventEmitter, OnDestroy, OnInit, Output } from '@angular/core';
import { UntypedFormControl, UntypedFormGroup } from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';
import { ResponsiveService } from '@tei/core';
import { ChipItemModel, ChipOptionModel, ChipType, StateOfChips } from '@tei/material';
import { Subscription } from 'rxjs';
import { PostSearchTransferTransactionsRequest, TransferStatusQuickFilterChipModel, TransferStatusTypeEnum, TransferTransactionSearchOptionEnum, TransferTransactionSearchOptionModel } from '../../@intermediate/transfer-transaction/model/transfer-transaction.model';
import moment from 'moment';

@Component({
  selector: 'tei-manage-transaction-inquiry-filter',
  templateUrl: './transaction-inquiry-filter.component.html',
  styleUrls: ['./transaction-inquiry-filter.component.scss'],

})
export class TransactionInquiryFilterComponent implements OnInit, AfterViewInit, OnDestroy
{
  @Output() enterFilterValueEvent = new EventEmitter<PostSearchTransferTransactionsRequest>();
  @Output() transferAllEvent = new EventEmitter<PostSearchTransferTransactionsRequest>();

  subscription = new Subscription();

  TransferTransactionSearchOptionEnum = TransferTransactionSearchOptionEnum;

  searchOptions = [
    { chipType: ChipType.TextArea, type: TransferTransactionSearchOptionEnum.ContactName, title: this.translate.instant('ADMIN_CENTER.TRANSACTION_INQUIRY.CONTACT_NAME') },
    { chipType: ChipType.RangeDatePicker, type: TransferTransactionSearchOptionEnum.TransactionTime, title: this.translate.instant('ADMIN_CENTER.TRANSACTION_INQUIRY.DATE') },
    { chipType: ChipType.TextArea, type: TransferTransactionSearchOptionEnum.TransferMessage, title: this.translate.instant('ADMIN_CENTER.TRANSACTION_INQUIRY.MESSAGE') }
  ] as Array<TransferTransactionSearchOptionModel>;

  ChipType = ChipType;
  form = new UntypedFormGroup({
    filter: new UntypedFormControl()
  });

  contactsRequest: PostSearchTransferTransactionsRequest = new PostSearchTransferTransactionsRequest();

  minTermLength = 0;
  chipItems: string[] = ['Status'];
  statusItemsWithDisplayName = [
    {
      chipName: 'TransferStatusTypes', displayName: this.translate.instant('ADMIN_CENTER.TRANSACTION_INQUIRY.STATUS')
      , selectedItem: ['Pending', 'Failed']
      , chipsState: StateOfChips.Selected
    },
  ];
  statusFilterChipDefaultData: Array<TransferStatusQuickFilterChipModel> = this.initTransferStatusData();

  isMobileView: boolean;

  constructor(private translate: TranslateService,
    private responsiveService: ResponsiveService,
    private changeDetectorRef: ChangeDetectorRef
  )
  {

  }

  ngOnInit(): void
  {
    this.contactsRequest.TransferStatusTypes = [TransferStatusTypeEnum.Pending, TransferStatusTypeEnum.Failed];
    this.load();
  }

  public load(): void
  {
    this.enterFilterValueEvent.emit(this.contactsRequest);
  }

  enterFilterValue($event: Event): void
  {
    $event?.stopPropagation();
    $event?.preventDefault();
    setTimeout(() => { this.load(); }, 300);
  }


  searchOptionChange(event: ChipOptionModel, option: TransferTransactionSearchOptionModel): void
  {
    if (option.chipType === ChipType.RangeDatePicker)
    {
      if (event.firstValue)
        this.contactsRequest.TransactionTimeStart = moment(event.firstValue).format("YYYY-MM-DD") ;
      else
        delete this.contactsRequest.TransactionTimeStart;

      if (event.secondValue)
        this.contactsRequest.TransactionTimeEnd = moment(event.secondValue).format("YYYY-MM-DD") ;
      else
        delete this.contactsRequest.TransactionTimeEnd;

    } else
    {
      if (event.firstValue)
      {
        this.contactsRequest[option.type] = event.firstValue;
      }
      else
      {
        delete this.contactsRequest[option.type];
      }
    }

    this.load();
  }

  onFilterChange(filterObjects: Array<ChipItemModel>): void
  {
    filterObjects.forEach(chipItem =>
    {
      if (chipItem.chipName === 'TransferStatusTypes')
      {
        this.contactsRequest[chipItem.chipName] = Array.from(chipItem.selectedItem).map(itemSelected =>
        {
          return this.getStatusKey(itemSelected as string);
        });
      }
      else
      {
        delete this.contactsRequest[chipItem.chipName];
      }
    });
    this.load();
  }

  getStatusKey(status: string): TransferStatusTypeEnum | undefined
  {
    const item = this.statusFilterChipDefaultData.find(d => d.TransferStatusTypes === status);
    return item ? this.calculateStateValue(item.TransferStatusTypes) : undefined;
  }

  calculateStateValue(value: string | null): TransferStatusTypeEnum | null
  {
    switch (value)
    {
      case 'Failed': return TransferStatusTypeEnum.Failed;
      case 'Successful': return TransferStatusTypeEnum.Successful;
      case 'Pending': return TransferStatusTypeEnum.Pending;
    }
    return null;
  }


  ngAfterViewInit()
  {
    this.responsiveService.mediaBreakpoint.subscribe(screenSize =>
    {
      this.isMobileView = screenSize === 'xs' || screenSize === 'sm';
      this.changeDetectorRef.detectChanges();
    });
  }

  initTransferStatusData(): Array<TransferStatusQuickFilterChipModel>
  {
    const statusMap = {
      Pending: 'ADMIN_CENTER.TRANSACTION_INQUIRY.PENDING',
      Successful: 'ADMIN_CENTER.TRANSACTION_INQUIRY.SUCCESSFUL',
      Failed: 'ADMIN_CENTER.TRANSACTION_INQUIRY.FAILED',
    };

    return Object.keys(statusMap).map(key =>
    {
      return { Status: this.translate.instant(statusMap[key]), TransferStatusTypes: key, selected: key !== 'Successful' };
    });
  }

  TransferAll()
  {
    this.transferAllEvent.emit();
  }

  ngOnDestroy(): void
  {
    this.subscription?.unsubscribe();
  }
}
