import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatDividerModule } from '@angular/material/divider';
import { MatLegacyAutocompleteModule as MatAutocompleteModule } from '@angular/material/legacy-autocomplete';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyCheckboxModule as MatCheckboxModule } from '@angular/material/legacy-checkbox';
import { MatLegacyChipsModule } from '@angular/material/legacy-chips';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacyPaginatorModule as MatPaginatorModule } from '@angular/material/legacy-paginator';
import { MatLegacyTableModule as MatTableModule } from '@angular/material/legacy-table';
import { MatMenuModule } from '@angular/material/menu';
import { MatSortModule } from '@angular/material/sort';
import { MatTooltipModule } from '@angular/material/tooltip';
import { RouterModule } from '@angular/router';
import { TeiCurrencyTranslateModule, TeiDateTranslateModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { BreadCrumbModule, LoadingModule } from '@tei/material';
import { TransferTransaction } from '../../../../@services/transfer-transaction.service';
import { TransactionInquiryFilterModule } from '../../../../components/transaction-inquiry/transaction-inquiry-filter.module';
import { TransferLogComponent } from './transfer-log.component';
import { SecureNavigationService } from '@tei/material-smart-components';

const BASE_MODULES = [
  CommonModule,
  FormsModule,
  ReactiveFormsModule,
];

const MATERIAL_MODULES = [MatInputModule, MatButtonModule, MatAutocompleteModule, MatCheckboxModule, MatSortModule, MatCardModule, MatInputModule,
  MatPaginatorModule, MatMenuModule, MatTooltipModule, MatLegacyChipsModule,
  MatTableModule, MatDividerModule];

const TEI_MATERIAL_MODULES = [
  BreadCrumbModule,
  LoadingModule
];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule, TransactionInquiryFilterModule, TeiDateTranslateModule, TeiCurrencyTranslateModule];
const COMPONENTS = [TransferLogComponent];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    RouterModule.forChild([
      { path: '', component: TransferLogComponent },
    ]),
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_CORE_MODULES
  ],
  providers: [{ provide: 'ITransferTransaction', useClass: TransferTransaction },
    { provide: 'ISecureNavigationService', useClass: SecureNavigationService }],
})
export class TransferLogModule { }
