import { SelectionModel } from '@angular/cdk/collections';
import { AfterViewInit, ChangeDetectorRef, Component, Inject, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { MatLegacyPaginator as MatPaginator } from '@angular/material/legacy-paginator';
import { MatSort } from '@angular/material/sort';
import { TranslateService } from '@ngx-translate/core';
import { ResponsiveService } from '@tei/core';
import { BreadCrumb, SnackBarService } from '@tei/material';
import { firstValueFrom, merge, Subscription } from 'rxjs';
import { PostSearchTransferTransactionsRequest, PostTransferTransactionsRequest, SortType, TransactionHeader, TransferStatusTypeEnum } from '../../../../@intermediate/transfer-transaction/model/transfer-transaction.model';
import { ITransferTransaction } from '../../../../@intermediate/transfer-transaction/transfer-transaction.interface';
import { PostNavigationRequest } from '@tei/material-smart-components/lib/@intermediate/secure-navigation/model/post-secure-navigation.model';
import { ISecureNavigationService } from '@tei/material-smart-components/lib/@intermediate/secure-navigation/secure-navigation.interface';
import { Router } from '@angular/router';

@Component({
  selector: 'tei-portal-admin-center-payment-engine-settings-transfer-log',
  templateUrl: './transfer-log.component.html',
  styleUrls: ['./transfer-log.component.scss']
})
export class TransferLogComponent implements OnInit, AfterViewInit, OnDestroy
{


  subscription: Subscription = new Subscription();

  form: FormGroup;
  isLoading = false;
  selection = new SelectionModel<TransactionHeader>(true, []);

  breadCrumbData: BreadCrumb[] = [
    {
      title: this.translate.instant('ADMIN_CENTER.HOST.SETTINGS.PORTAL_ADMIN_CENTER'),
      relativeUrl: ''
    },
    {
      title: this.translate.instant('ADMIN_CENTER.HOST.SETTINGS.SETUP'),
      relativeUrl: '../../../'
    },
    {
      title: this.translate.instant('ADMIN_CENTER.HOST.SETTINGS.PAYMENT_ENGINE'),
      relativeUrl: '../../'
    },
    {
      title: this.translate.instant('ADMIN_CENTER.HOST.SETTINGS.TRANSACTION_INQUIRY'),
      relativeUrl: '../'
    },
    {
      title: this.translate.instant('ADMIN_CENTER.TRANSACTION_INQUIRY.TRANSFER_LOG_TITLE'),
      relativeUrl: ''
    }
  ];

  dataSource: TransactionHeader[] = [];
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;

  totalCount = 0;
  pageSize = 10;
  displayedColumns: string[] = ['Select', 'TransactionUID', 'Total', 'Contact', 'Status', 'Message', 'MobileMode'];
  pageIndex = 1;
  requestModel = {} as PostSearchTransferTransactionsRequest;

  isSmallScreen = false;
  screenSize = '';
  innerWidth: number;
  get mobileMode(): boolean { return (this.innerWidth <= 960 || (this.screenSize === 'xs' || this.screenSize === 'sm')); }


  constructor(private translate: TranslateService, private snackBarService: SnackBarService,
    private responsiveService: ResponsiveService,
    @Inject('ITransferTransaction') private transferTransactionService: ITransferTransaction,
    private changeDetectorRef: ChangeDetectorRef,
    @Inject('ISecureNavigationService') private secureNavigationService: ISecureNavigationService,
    private router: Router,)
  {
  }

  async ngOnInit()
  {
    this.handleScreenResize();
    await this.loadSecureNavigation();
  }

  ngAfterViewInit(): void
  {
    this.handleScreenSizeChangeEvent();

    this.handleSortEvent();

    this.handlePaginationEvent();

    this.changeDetectorRef.detectChanges();
  }

  private handleScreenResize(): void
  {
    this.subscription.add(this.responsiveService.mediaBreakpoint.subscribe(screenSize =>
    {
      this.innerWidth = window.innerWidth;
      this.screenSize = screenSize;
    }));
  }

  async load(request: PostSearchTransferTransactionsRequest): Promise<void>
  {
    this.requestModel = request;
    this.requestModel.PageNumber = this.pageIndex + 1;
    this.requestModel.PageSize = this.pageSize;

    if (this.sort.active && this.sort.direction)
    {
      this.requestModel.SortOrder = this.sort.active;
      this.requestModel.SortType = this.sort.direction === 'desc' ? SortType.Descending : SortType.Ascending;
    } else
    {
      delete this.requestModel.SortOrder;
      delete this.requestModel.SortType;
    }

    this.isLoading = true;
    try
    {
      const response = await firstValueFrom(this.transferTransactionService.Search(request));

      if (!response?.TransactionHeaders?.length)
      {
        this.snackBarService.showError(this.translate.instant('ADMIN_CENTER.TRANSACTION_INQUIRY.TRANSFER_LOG.ITEM_NOT_FOUND_MESSAGE'));
        this.dataSource = [];
        this.totalCount = 0;
        return;
      }

      this.dataSource = response.TransactionHeaders;
      this.totalCount = response.TotalCount;


    } catch (error)
    {
      console.log(error);
    }
    finally
    {
      this.isLoading = false;
      this.selection.clear();
      this.changeDetectorRef.detectChanges();
    }

  }

  private handlePaginationEvent(): void
  {
    merge(this.sort.sortChange, this.paginator.page).subscribe(() =>
    {
      this.pageIndex = this.paginator.pageIndex;
      this.pageSize = this.paginator.pageSize;
      this.load(this.requestModel);
    });
  }

  private handleSortEvent(): void
  {
    this.sort.sortChange.subscribe(() =>
    {
      this.resetPaging();
    });
  }

  private handleScreenSizeChangeEvent(): void
  {
    this.subscription.add(this.responsiveService.mediaBreakpoint.subscribe(screenSize =>
    {
      this.isSmallScreen = (screenSize === 'md' || screenSize === 'sm' || screenSize === 'xs');
      this.changeDetectorRef.detectChanges();
    }));
  }

  private resetPaging(): void
  {
    this.pageIndex = 0;
    this.paginator.pageIndex = 0;
  }

  public loadHandler(request: PostSearchTransferTransactionsRequest): void
  {
    this.requestModel = request;
    this.resetPaging();
    this.load(this.requestModel);
  }

  calculateStateValue(value: TransferStatusTypeEnum): string
  {
    switch (value)
    {
      case TransferStatusTypeEnum.Pending: return this.translate.instant('ADMIN_CENTER.TRANSACTION_INQUIRY.TRANSFER_LOG.PENDING');
      case TransferStatusTypeEnum.Successful: return this.translate.instant('ADMIN_CENTER.TRANSACTION_INQUIRY.TRANSFER_LOG.SUCCESSFUL');
      case TransferStatusTypeEnum.Failed: return this.translate.instant('ADMIN_CENTER.TRANSACTION_INQUIRY.TRANSFER_LOG.FAILED');
    }
    return null;
  }

  /** Whether the number of selected elements matches the total number of rows. */
  isAllSelected(): boolean
  {
    const numSelected = this.selection.selected.length;
    const numRows = this.dataSource.length;
    return numSelected === numRows;
  }

  /** Selects all rows if they are not all selected; otherwise clear selection. */
  masterToggle(): void
  {
    if (this.isAllSelected())
    {
      this.selection.clear();
      return;
    }

    this.selection.select(...this.dataSource);
  }

  /** The label for the checkbox on the passed row */
  checkboxLabel(row?: TransactionHeader): string
  {
    if (!row)
    {
      return `${ this.isAllSelected() ? 'deselect' : 'select' } all`;
    }
    return `${ this.selection.isSelected(row) ? 'deselect' : 'select' } row ${ row.TransactionUID }`;
  }

  async transferSelected(): Promise<void>
  {
    if (this.selection?.selected?.length)
    {
      this.isLoading = true;
      try
      {

        await firstValueFrom(this.transferTransactionService.Transfer({
          ContactID: this.requestModel.ContactID,
          ContactName: this.requestModel.ContactName,
          TransactionTimeEnd: this.requestModel.TransactionTimeEnd,
          TransactionTimeStart: this.requestModel.TransactionTimeStart,
          TransactionUIDs: this.selection?.selected.map(a => a.TransactionUID),
          TransferMessage: this.requestModel.TransferMessage,
          TransferStatusTypes: this.requestModel.TransferStatusTypes

        } as PostTransferTransactionsRequest));

        this.snackBarService.showSuccess(this.translate.instant('ADMIN_CENTER.TRANSACTION_INQUIRY.TRANSFER_LOG.SUCCESS_MESSAGE'));

        this.load(this.requestModel).then();

      } catch (error)
      {
        console.log(error);
      }
      finally
      {
        this.isLoading = false;
      }

    }
  }

  async transferAll(): Promise<void>
  {

    this.isLoading = true;
    try
    {
      await firstValueFrom(this.transferTransactionService.Transfer({
        ContactID: this.requestModel.ContactID,
        ContactName: this.requestModel.ContactName,
        TransactionTimeEnd: this.requestModel.TransactionTimeEnd,
        TransactionTimeStart: this.requestModel.TransactionTimeStart,
        TransferMessage: this.requestModel.TransferMessage,
        TransferStatusTypes: this.requestModel.TransferStatusTypes

      } as PostTransferTransactionsRequest));

      this.snackBarService.showSuccess(this.translate.instant('ADMIN_CENTER.TRANSACTION_INQUIRY.TRANSFER_LOG.SUCCESS_MESSAGE'));

      this.load(this.requestModel).then();

    } catch (error)
    {
      console.log(error);
    }
    finally
    {
      this.isLoading = false;
    }
  }

  async loadSecureNavigation(): Promise<void>
  {
    const request: PostNavigationRequest = {
      WebItemGroupIdentifier: 'portal',
      WebItemIdentifiers: [{Identifier : 'payment-settings'}]
    };
    await firstValueFrom(this.secureNavigationService.Post(request));
  }


  ngOnDestroy(): void
  {
    this.subscription?.unsubscribe();
  }
}
