import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { PostSearchTransferTransactionsRequest, PostSearchTransferTransactionsResponse, PostTransferTransactionsRequest, PostTransferTransactionsResponse } from '../@intermediate/transfer-transaction/model/transfer-transaction.model';
import { ITransferTransaction } from '../@intermediate/transfer-transaction/transfer-transaction.interface';


@Injectable({
  providedIn: 'root'
})
export class TransferTransaction implements ITransferTransaction
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Admin/AdminCenter/TransferTransaction/';
  }

  Search(request: PostSearchTransferTransactionsRequest): Observable<PostSearchTransferTransactionsResponse>
  {
    const apiUrl = this.serviceUrl + 'Search';
    return this.http.post<PostSearchTransferTransactionsResponse>(apiUrl, request);
  }

  Transfer(request: PostTransferTransactionsRequest): Observable<PostTransferTransactionsResponse>
  {
    const apiUrl = this.serviceUrl + 'Transfer';
    return this.http.post<PostTransferTransactionsResponse>(apiUrl, request);
  }

}

