import { ChipType } from "@tei/material";

export enum TransferStatusTypeEnum
{
    Pending,
    Successful,
    Failed
}

export class PostSearchTransferTransactionsRequest
{
    TransferStatusTypes?: Array<TransferStatusTypeEnum>;
    ContactID?: string;
    ContactName?: string;
    TransferMessage?: string;
    TransactionUIDs?: Array<string>;
    TransactionTimeStart?: string;
    TransactionTimeEnd?: string;
    PageNumber: number;
    PageSize: number;
    SortOrder: string;
    SortType: number;
}

export enum TransferTransactionSearchOptionEnum
{
    TransferStatusTypes = 'TransferStatusTypes',
    ContactID = 'ContactID',
    ContactName = 'ContactName',
    TransferMessage = 'TransferMessage',
    TransactionUIDs = 'TransactionUIDs',
    TransactionTime = 'TransactionTime',
}

export class TransferTransactionSearchOptionModel
{
    type: TransferTransactionSearchOptionEnum;
    chipType: ChipType;
    title: string;
}


export class TransactionHeader
{
    TransactionUID: string;
    TransactionTime: Date;
    CustomerName: string;
    Total: number;
    TransferStatus: TransferStatusTypeEnum;
    TransferMessage: string;
}

export class PostSearchTransferTransactionsResponse 
{
    TransactionHeaders: Array<TransactionHeader>;
    TotalCount: number;
}

export class PostTransferTransactionsRequest
{
    TransferStatusTypes?: Array<TransferStatusTypeEnum>;
    ContactID?: string;
    ContactName?: string;
    TransferMessage?: string;
    TransactionUIDs?: Array<string>;
    TransactionTimeStart?: string;
    TransactionTimeEnd?: string;
}

export class PostTransferTransactionsResponse
{

}


export class TransferStatusQuickFilterChipModel
{
    Status: string;
    TransferStatusTypes?: string;
}

export enum SortType
{
    Ascending = 0,
    Descending = 1
}